.NavbarLeftLayout {
  -webkit-animation-name: showNavbarLeft;
          animation-name: showNavbarLeft;
  -webkit-animation-duration: .6s;
          animation-duration: .6s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  position: fixed;
  width: 217px;
  height: calc( 100vh - 55px);
  background: #f1d47a; }

.NavbarLeftLayout.active {
  -webkit-animation-name: hideNavbarLeft;
          animation-name: hideNavbarLeft;
  -webkit-animation-duration: .6s;
          animation-duration: .6s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: .2s;
          animation-delay: .2s; }
  .NavbarLeftLayout.active .Perfil {
    padding-top: 25px;
    padding-left: 34px;
    -webkit-animation-name: hidePerfil;
            animation-name: hidePerfil;
    -webkit-animation-duration: .2s;
            animation-duration: .2s;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }
  .NavbarLeftLayout.active .Perfil-nombre {
    opacity: 0; }
  .NavbarLeftLayout.active .Perfil-rol {
    opacity: 0; }
  .NavbarLeftLayout.active .Navegacion-li {
    padding-left: 23px; }
    .NavbarLeftLayout.active .Navegacion-li:hover .Navegacion-txt {
      display: inline-block;
      position: absolute;
      padding-left: 28px;
      opacity: .9; }
    .NavbarLeftLayout.active .Navegacion-li .Navegacion-txt {
      opacity: 0; }

@-webkit-keyframes hideNavbarLeft {
  from {
    width: 217px; }
  to {
    width: 70px; } }

@keyframes hideNavbarLeft {
  from {
    width: 217px; }
  to {
    width: 70px; } }

@-webkit-keyframes showNavbarLeft {
  from {
    width: 70px; }
  to {
    width: 217px; } }

@keyframes showNavbarLeft {
  from {
    width: 70px; }
  to {
    width: 217px; } }

@-webkit-keyframes hidePerfil {
  from {
    height: 145px;
    padding-top: 25px;
    padding-left: 34px; }
  to {
    height: 90px;
    padding-top: 20px;
    padding-left: 10px; } }

@keyframes hidePerfil {
  from {
    height: 145px;
    padding-top: 25px;
    padding-left: 34px; }
  to {
    height: 90px;
    padding-top: 20px;
    padding-left: 10px; } }



