@-webkit-keyframes navigationLink {
  from {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    opacity: .8; }
  50% {
    opacity: .6; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@keyframes navigationLink {
  from {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    opacity: .8; }
  50% {
    opacity: .6; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@-webkit-keyframes rounded {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rounded {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.InicioFoot {
  font-family: "Open Sans", sans-serif;
  padding-top: 20px;
  text-align: center;
  font-size: .82em;
  color: #666; }

.InicioFoot-texto {
  display: block;
  padding-bottom: 9px; }

.InicioFoot-btn {
  border: 1px solid #ececec;
  width: 300px;
  height: 30px;
  border-radius: 4px;
  color: #999;
  background: white;
  cursor: pointer; }
  .InicioFoot-btn:hover {
    background: #eaeaea; }

.InicioFoot-copy {
  display: block;
  padding-top: 9px;
  font-size: .95em; }

@media only screen and (max-width: 350px) {
  .InicioFoot-btn {
    width: 280px; } }



