@-webkit-keyframes navigationLink {
  from {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    opacity: .8; }
  50% {
    opacity: .6; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@keyframes navigationLink {
  from {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    opacity: .8; }
  50% {
    opacity: .6; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@-webkit-keyframes rounded {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rounded {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.Perfil {
  background: url("../../images/background-perfil.jpg");
  height: 145px;
  padding-top: 25px;
  padding-left: 34px;
  font-family: "Open Sans", sans-serif;
  font-size: .8em;
  -webkit-animation-name: showPerfil;
          animation-name: showPerfil;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.Perfil-imagen {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #ddd; }

.Perfil-nombre {
  margin-top: 9px;
  display: block;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 1; }

.Perfil-rol {
  color: #5c5c5c;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 1; }

@-webkit-keyframes showPerfil {
  from {
    height: 90px;
    padding-top: 20px;
    padding-left: 10px; }
  to {
    height: 145px;
    padding-top: 25px;
    padding-left: 34px; } }

@keyframes showPerfil {
  from {
    height: 90px;
    padding-top: 20px;
    padding-left: 10px; }
  to {
    height: 145px;
    padding-top: 25px;
    padding-left: 34px; } }

@-webkit-keyframes elementDisappear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes elementDisappear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }



