.Modal {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: auto; }

.Modal-content {
  width: 95%;
  height: 95%;
  background: #ddd;
  border-radius: 10px;
  -webkit-animation-name: elementIn;
          animation-name: elementIn;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  will-change: transform, opacity;
  border: 12px solid white; }

@-webkit-keyframes elementIn {
  from {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    opacity: 0; }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; } }

@keyframes elementIn {
  from {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    opacity: 0; }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; } }

@-webkit-keyframes elementOut {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; }
  to {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    opacity: 0; } }

@keyframes elementOut {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1; }
  to {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    opacity: 0; } }



