@-webkit-keyframes navigationLink {
  from {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    opacity: .8; }
  50% {
    opacity: .6; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@keyframes navigationLink {
  from {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
    opacity: .8; }
  50% {
    opacity: .6; }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 1; } }

@-webkit-keyframes rounded {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rounded {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.LoginLogo {
  font-family: "Open Sans", sans-serif; }

.LoginLogo-titulo {
  font-family: "Cabin", sans-serif;
  font-size: 1.2em;
  text-align: center;
  color: #666; }

.LoginLogo-texto {
  font-size: .85em;
  text-align: center; }

.LoginLogo-subtitulo {
  display: block;
  font-size: .82em;
  font-weight: normal;
  color: #666;
  text-align: center; }



